<template>
  <div class="classify">
    <div class="channels">
      <h3>男生频道</h3>
      <div class="channel">
        <div v-for="(o,index) in classifyList.maleObj">
          <div class="ch">
            <img src="../../static/douluo.jpg">
            <p>{{o.name}}</p>
            <p>{{o.num}}本</p>
          </div>
          <div class="sl" v-if="index%2===0"></div>
        </div>
      </div>
    </div>
    <div class="channels">
      <h3>女生频道</h3>
      <div class="channel">
        <div v-for="(o,index) in classifyList.femaleObj">
          <div class="ch">
            <img src="../../static/douluo.jpg">
            <p>{{o.name}}</p>
            <p>{{o.num}}本</p>
          </div>
          <div class="sl" v-if="index%2===0"></div>
        </div>
      </div>
    </div>
    <div class="channels">
      <h3>出版频道</h3>
      <div class="channel">
        <div v-for="(o,index) in classifyList.publishObj">
          <div class="ch">
            <img src="../../static/douluo.jpg">
            <p>{{o.name}}</p>
            <p>{{o.num}}本</p>
          </div>
          <div class="sl" v-if="index%2===0"></div>
        </div>
      </div>
      <div class="total">
        一共<span>{{classifyList.total}}</span>部作品,本周新增<span>{{classifyList.newlyAdded}}</span>部作品
      </div>
    </div>
  </div>
</template>
<script>
  import {Indicator} from 'mint-ui';
  import axios from 'axios';
  export default {
    data(){
      return {
        classifyList:{}
      }
    },
    created() {
      this.getList();
    },
    methods:{
      getList(){
        Indicator.open('加载中');
        axios.get('/vueBook/classify')
        .then((res)=>{
          if(res.data.state === 200){
            this.classifyList = res.data.data;
            Indicator.close();
          }
        }).catch((error)=>{

        });
      }
    }
  }
</script>
<style scoped>
  p {
    margin:0;
  }
  h3 {
    margin:0;
    text-align: left;
    border-left: 3px solid red;
    padding-left: 0.3rem;
    margin-left :1rem;
  }
  .classify {
    margin-top:3.5rem;
  }
  .channel {
    padding: 0.5rem 1.5rem;
    overflow: hidden;
    clear:both;
  }
  .channels {
    margin-bottom: 1rem;
  }
  .ch {
    text-align: left;
    float: left;
    width:40%;
    line-height: 4rem;
    height:4rem;
    padding:0.5rem 0;
  }
  .ch img {
    width: 3rem;
    height: 4rem;
  }
  .ch p {
    display: inline-block;
    position: absolute;
    float: right;
    line-height: 2rem;
    height: 2rem;
    padding-left:0.5rem;
    font-family: 宋体;
    font-size: 14px;
  }
  .ch p:last-child {
    margin-top:2rem;
    font-size: 12px;
    color: #666;
  }
  .sl {
    border-right: 1px solid #ddd;
    height: 2rem;
    width: 1px;
    float: left;
    margin: 1.5rem 1.5rem;
  }
  .total {
    font-size: 14px;
    color: #888;
  }
</style>


